import React, { useState } from "react";
import "./App.css";
import FrontCanvas from "./components/FrontCanvas";
import Prize from "./components/Prize";

const config = {
  width: 600,
  height: 600,
};

function App() {
  const [show, setShow] = useState(false);

  const showPrize = () => {
    setShow(true);
  };
  return (
    <div className="App">
      <Prize showPrize={show} />
      <FrontCanvas
        width={config.width}
        height={config.height}
        onFinished={showPrize}
      />
    </div>
  );
}

export default App;
