import React, { useEffect, useRef } from "react";
import goldBackground from "../images/gold.svg";
import dollarSign from "../images/dollar.svg";
import brushImage from "../images/goldcoin-cursor.png";
import "./FrontCanvas.css";

const defaultStride = 32;
export default function FrontCanvas({ width, height, onFinished }) {
  const ref = useRef(null);
  const scratching = useRef(false);
  const size = useRef(0);
  const requestRef = useRef(null);

  const setScratching = (status) => {
    scratching.current = status;
  };
  const scratch = (brush, ctx, x, y) => {
    if (scratching.current) {
      ctx.globalCompositeOperation = "destination-out";
      ctx.drawImage(brush, x, y, brush.width, brush.height);
      let req = handlePercentage(getFilledInPixels(defaultStride));
      if (req) {
        requestRef.current = req;
      }
    }
  };

  useEffect(() => {
    const canvas = ref.current;
    const ctx = canvas.getContext("2d");
    console.log(ctx);
    const background = new Image();
    background.src = goldBackground;
    background.onload = () => {
      ctx.drawImage(background, 0, 0, canvas.width, canvas.height);
    };
    const image = new Image();
    image.src = dollarSign;
    image.onload = () => {
      for (var i = 0; i < 3; i++) {
        for (var j = 0; j < 3; j++) {
          ctx.drawImage(image, 25 + i * 200, 25 + j * 200, 150, 150);
        }
      }
    };

    const brush = new Image();
    brush.src = brushImage;
    brush.onload = () => {
      console.log("ready");
    };
    canvas.addEventListener("mouseup", () => {
      setScratching(false);
    });

    canvas.addEventListener("mousedown", () => {
      setScratching(true);
    });

    canvas.addEventListener("mousemove", (e) => {
      let x = e.offsetX;
      let y = e.offsetY;
      scratch(brush, ctx, x, y);
    });
  }, []);

  const getFilledInPixels = (stride) => {
    if (!stride || stride < 1) {
      stride = 1;
    }
    const canvas = ref.current;
    const ctx = canvas.getContext("2d");

    const pixels = ctx.getImageData(0, 0, canvas.width, canvas.height);
    const pdata = pixels.data;
    const len = pdata.length;
    const total = len / stride;
    let count = 0;

    for (var i = 0; i < len; i += stride) {
      if (parseInt(pdata[i]) === 0) {
        count++;
      }
    }
    return Math.round((count / total) * 100);
  };

  const handlePercentage = (filled) => {
    filled = filled || 0;
    if (filled > 75) {
      return requestAnimationFrame(autoScratching);
    } else {
      return null;
    }
  };
  const autoScratching = (tframe) => {
    size.current += parseInt(tframe / 1000);
    const canvas = ref.current;
    const ctx = canvas.getContext("2d");
    ctx.globalCompositeOperation = "destination-out";
    ctx.beginPath();
    ctx.fillStyle = "green";
    ctx.arc(
      canvas.width / 2,
      canvas.height / 2,
      size.current / 10,
      0,
      2 * Math.PI
    );
    ctx.closePath();
    ctx.fill();
    if (getFilledInPixels(defaultStride) > 99) {
      window.cancelAnimationFrame(requestRef.current);
      if (canvas.parentNode) {
        canvas.parentNode.removeChild(canvas);
      }
      onFinished();
    } else {
      requestAnimationFrame(autoScratching);
    }
  };
  return (
    <canvas
      className="FrontCanvas"
      ref={ref}
      width={width}
      height={height}
    ></canvas>
  );
}
